import './Apropos.scss';
import Fade from 'react-reveal/Fade';

export default function Apropos(){
    return(
      <div  className="Apropos">
          <div className="contenu">
            <h2><span> propos de moi</span></h2>
            <Fade left>
              <div className="gauche">
              <p>Bienvenue! Mon nom est <span>Monica Lonard</span></p>
              </div>
            </Fade>
            <Fade left delay={500}>
              <div className="milieu">
              <p>Je suis une tudiante en techniques d'intgration multimdia au Collge de Maisonneuve</p>
              </div>
            </Fade>
            <Fade right delay={1000}>
              <div className="droite">
              <p>Je suis autant passionne par la programmation et le design que par les jeux vidos</p>
              </div>
            </Fade>
        </div>
      </div>
    )
}